.prayer ol {
	text-align: left;
	font-size: calc(10px + 0.5vmin);
	margin: 10px;
}

.prayer h5 {
	padding: 0;
	margin: 10px;
	font-size: calc(10px + 1vmin);
}

.vaccine {
	text-align: left;
	font-size: calc(10px + 0.5vmin);
	padding: 50px;
}

.generic-info h5 {
	padding: 0;
	margin: 10px;
	font-size: calc(10px + 1vmin);
}

.generic-info div {
	text-align: left;
	font-size: calc(10px + 0.5vmin);
	padding: 0px 50px;
}

.info-carousel {
	width: 400px;
	margin: 0 auto;
}

@media screen and (max-width: 500px) {
	.info-carousel { width: 300px; }
}
