.ct-labels span {
    color: white;
}

.ct-grids line {
    stroke: white;
}

.all-charts > div {
    display: inline-block;
}

.all-charts .active .ct-series-a .ct-line {
  stroke: #e34234;
}

.all-charts .confirmed .ct-series-a .ct-line {
  stroke: #ffff66;
}

.all-charts .discharged .ct-series-a .ct-line {
  stroke: #7fff00;
}

.all-charts .active .ct-series-a .ct-point {
  stroke: #e34234;
}

.all-charts .confirmed .ct-series-a .ct-point {
  stroke: #ffff66;
}

.all-charts .discharged .ct-series-a .ct-point {
  stroke: #7fff00;
}

.seriesChart > h5 {
    font-size: calc(10px + 0.5vmin);
    text-transform: capitalize;
}

.ct-chart-bar {
  overflow: visible;
  margin : 0 0 30px 0;
}

.ct-chart .ct-label.ct-horizontal.ct-end {
  position: relative;
  justify-content: flex-end;
  text-align: right;
  transform-origin: 100% 0;
  transform: translate(-100%) rotate(-45deg);
  white-space:nowrap;
  font-size: 10px;
}

.quarantine-chart {
    font-size: calc(10px + 0.5vmin);
}

.quarantine-chart h4 {
    padding-bottom: 0px;
    margin-top: 40px;
    margin-bottom: 0px;
}
.recharts-responsive-container {
  margin : 0 auto;
}

.app-pie-charts > div {
    display: inline-block;
}


